
.marker-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  font-size: 0;
  z-index: 1;
}
.marker-wrapper .marker {
    position: relative;
    font-size: 16px;
}
.marker-wrapper .marker .time-info-wrapper {
      position: absolute;
      top: -48px;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
}
.marker-wrapper .marker .time-info-wrapper.v-enter {
        opacity: 0;
}
.marker-wrapper .marker .time-info-wrapper.v-enter-active {
        -webkit-transition: opacity linear .2s;
        transition: opacity linear .2s;
}
.marker-wrapper .marker .time-info-wrapper .time-info {
        display: inline-block;
        padding: 10px 12px;
        font-size: 12px;
        font-weight: 50;
        white-space: nowrap;
        border-radius: 16px;
        -webkit-box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2), -1px 0 1px rgba(0, 0, 0, 0.2);
                box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2), -1px 0 1px rgba(0, 0, 0, 0.2);
        background-color: #fff;
}
.marker-wrapper .marker .time-info-wrapper .time-info .dot-wrapper .dot {
          background-color: gray;
}
.marker-wrapper .marker .time-info-wrapper .time-info .time {
          color: #fc9153;
}
.marker-wrapper .marker .time-info-wrapper .arrow {
        position: absolute;
        left: 50%;
        border-width: 5px;
        border-style: solid;
        -webkit-box-shadow: 2px -2px 2px rgba(0, 0, 0, 0.2);
                box-shadow: 2px -2px 2px rgba(0, 0, 0, 0.2);
        z-index: 10;
        bottom: -3px;
        -webkit-transform: translateX(-50%) rotate(135deg);
                transform: translateX(-50%) rotate(135deg);
        border-color: #fff #fff transparent transparent;
}



