
.dot-wrapper {
  width: 100%;
  height: 100%;
}
.dot-wrapper .dot {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #fff;
}
.dot-wrapper .dot + .dot {
      margin-left: 5px;
}
.dot-wrapper .dot.dot-1 {
      -webkit-animation: scale .5s infinite linear;
              animation: scale .5s infinite linear;
}
.dot-wrapper .dot.dot-2 {
      -webkit-animation: scale .5s .1s infinite linear;
              animation: scale .5s .1s infinite linear;
}
.dot-wrapper .dot.dot-3 {
      -webkit-animation: scale .5s .2s infinite linear;
              animation: scale .5s .2s infinite linear;
}
@-webkit-keyframes scale {
0% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
20% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes scale {
0% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
20% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}



