
.owner {
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}
.owner .pic {
    position: relative;
}
.owner .pic .text-wrapper {
      position: fixed;
      bottom: 90px;
      right: 20px;
}
.owner .pic .text-wrapper .title {
        margin-bottom: 20px;
        font-size: 20px;
        font-weight: 700;
}
.owner .pic .blur {
      position: absolute;
      background: white;
      width: 100%;
      height: 30px;
      bottom: -6px;
      -webkit-filter: blur(4px);
              filter: blur(4px);
}
.owner .button {
    position: absolute;
    bottom: 15px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 80vw;
    height: 30px;
    font-weight: 400;
    font-size: 14px;
    text-align: center;
    line-height: 30px;
    border-radius: 15px;
    background-color: #fc9153;
    color: white;
}
.owner.slide-left-enter, .owner.slide-left-leave-to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.owner.slide-left-enter-active, .owner.slide-left-leave-active {
    -webkit-transition: -webkit-transform linear .2s;
    transition: -webkit-transform linear .2s;
    transition: transform linear .2s;
    transition: transform linear .2s, -webkit-transform linear .2s;
}



