
.sharing-wrapper {
  position: relative;
  z-index: 15;
}
.sharing-wrapper .sharing-header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0 10px;
    height: 30px;
    background-color: #fff;
}
.sharing-wrapper .sharing-header .header-left {
      font-size: 0px;
      font-weight: 200;
      color: #000;
}
.sharing-wrapper .sharing-header .header-left a {
        position: relative;
        display: inline-block;
        font-size: 14px;
}
.sharing-wrapper .sharing-header .header-left a.tab-passenger {
          margin-right: 15px;
}
.sharing-wrapper .sharing-header .header-left a.tab-driver {
          margin-right: 5px;
}
.sharing-wrapper .sharing-header .header-left a.router-active {
          color: #fc9153;
}
.sharing-wrapper .sharing-header .header-left a .underline {
          position: absolute;
          left: 0;
          bottom: -3px;
          display: inline-block;
          width: 100%;
          height: 1px;
          -webkit-transition: -webkit-transform .4s linear;
          transition: -webkit-transform .4s linear;
          transition: transform .4s linear;
          transition: transform .4s linear, -webkit-transform .4s linear;
          background-color: #fc9153;
}
.sharing-wrapper .sharing-header .header-left .title {
        position: relative;
        display: inline-block;
        vertical-align: top;
        background-color: red;
        border-radius: 3px;
}
.sharing-wrapper .sharing-header .header-left .title .angle {
          position: absolute;
          left: -3px;
          top: 4px;
          border-width: 5px;
          border-style: solid;
          border-color: transparent red red transparent;
}
.sharing-wrapper .sharing-header .header-left .title .text {
          position: relative;
          top: 3px;
          display: inline-block;
          height: 14px;
          font-size: 10px;
          font-weight: 200;
          color: #fff;
}
.sharing-wrapper .sharing-header .header-right .icon {
      display: inline-block;
      vertical-align: middle;
      margin-left: 15px;
}
.sharing-wrapper .sharing-content-wrapper {
    position: absolute;
    top: 30px;
    width: 100%;
}
.sharing-wrapper .sharing-content-wrapper .sharing-content {
      position: absolute;
      height: 100%;
      width: 100%;
}
.sharing-wrapper .sharing-content-wrapper .out_of_screen {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
}
.sharing-wrapper .sharing-content-wrapper .out_of_screen.out_of_screen-left {
        left: -100%;
}
.sharing-wrapper .sharing-content-wrapper .out_of_screen.out_of_screen-right {
        right: -100%;
}



