
.customer {
  text-align: center;
  line-height: 100px;
  background: #f5f5f5;
}
.customer.slide-right-enter, .customer.slide-right-leave-to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.customer.slide-right-enter-active, .customer.slide-right-leave-active {
    -webkit-transition: -webkit-transform linear .2s;
    transition: -webkit-transform linear .2s;
    transition: transform linear .2s;
    transition: transform linear .2s, -webkit-transform linear .2s;
}



