
.header-wrapper {
  position: relative;
  padding: 10px 10px 15px;
  background-color: #fff;
  -webkit-box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.2);
  z-index: 10;
}
.header-wrapper .header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin-bottom: 15px;
}
.header-wrapper .header .city {
      line-height: 30px;
}
.header-wrapper .header .city .text {
        display: inline-block;
        margin-right: 5px;
}
.header-wrapper .header .city .icon {
        position: relative;
        top: 2px;
}
.header-wrapper .tab {
    position: relative;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
}
.header-wrapper .tab ul {
      width: 425px;
      height: 16px;
      font-size: 0;
}
.header-wrapper .tab ul .type {
        display: inline-block;
        padding: 0 12px;
        font-size: 14px;
        font-weight: 200;
        line-height: 16px;
        color: #000;
}
.header-wrapper .tab ul .type.router-active {
          color: #fc9153;
}
.header-wrapper .tab ul .type:first-child {
          padding-left: 0;
}
.header-wrapper .tab .all-type {
      position: absolute;
      top: 0;
      right: 0;
      height: 16px;
      -webkit-box-shadow: -5px 0 3px #fff;
              box-shadow: -5px 0 3px #fff;
      background-color: #fff;
}



